import React from 'react';
import { Header, CardGrid, Card, Div, platform, IOS, Panel, Group, PanelHeader, PanelHeaderButton } from '@vkontakte/vkui';


//иконки назад
import Icon28ArrowLeftOutline from '@vkontakte/icons/dist/28/arrow_left_outline'; //ios
import Icon24Back from '@vkontakte/icons/dist/24/back'; //android


const osname = platform();

const News = props => (
    <Panel id={props.id}>
        <PanelHeader
        left={<PanelHeaderButton onClick={props.go} data-to="home">
        {osname === IOS ? <Icon28ArrowLeftOutline/> : <Icon24Back/>}
    </PanelHeaderButton>}
        >
            Новости
        </PanelHeader>
       <Group>
       

       <CardGrid>
          <Card size="l" mode="shadow">
          {<Header mode="secondary">22 апр в 13:30</Header>}
          <Div style={{ paddingTop: 1, paddingBottom: 12, color: 'black' }}>
          По данным на 22 апреля в Курганской области зарегистрировано 2 новых
           подтвержденных случая COVID-19. Это жители города Кургана и города
            Щучье. Случаи завозные, граждане прибыли из Москвы. Они 
            госпитализированы в Курганскую областную инфекционную больницу.
             Членам их семей Управлением Роспотребнадзора выданы постановления 
             об обязательной самоизоляции сроком на 14 дней.
          <br/>
          <br/>
          Всего с момента начала регистрации заболеваемости в регионе 
          зафиксировано 22 случая новой коронавирусной инфекции. 19 
          человек находятся на лечении в инфекционной больнице.
           3 – выписаны по выздоровлению.
          </Div>
            <div style={{ height: 1 }} />
          </Card>
        </CardGrid>


       <CardGrid>
          <Card size="l" mode="shadow">
          {<Header mode="secondary">17 апр в 11:14</Header>}
          <Div style={{ paddingTop: 1, paddingBottom: 12, color: 'black' }}>
          16 апреля в Курганской области зарегистрирован новый, 13-й, 
          случай COVID-19. Заболевший – мужчина, который приехал в 
          Курган из Москвы. Когда почувствовал себя плохо, обратился 
          за медицинской помощью. Сейчас мужчина госпитализирован в 
          инфекционную больницу. Управлением Роспотребнадзора по 
          Курганской области организовано проведение профилактических 
          мероприятий устанавливается круг контактных лиц.
          <br/>
          <br/>
          Всего в инфекционной больнице на лечении сейчас находятся 
          10 пациентов с диагнозом «новая коронавирусная инфекция». 
          Трое – выписаны по выздоровлению.
          </Div>
            <div style={{ height: 1 }} />
          </Card>
        </CardGrid>
        

    </Group>

    </Panel>
);

export default News;




